@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oxanium&display=swap);
body {
  margin: 0;
  font-family: 'Poppins';
}
.board {
  width: 480px;
  height: 480px;
  margin: auto;
  margin-top: 100px;

}

.header {
  background-color: #ee3453;
  color: white;
  height: 75vh;
  width: 100%;
  top: 0;
  padding-top: 25vh;
}

.inputContainer {
  display: flex;
  width: 350px;
  margin-bottom: 15px;
  background-color: white;
  border-radius: 25px;
  height: 50px;
}

.head {
  height: 100px;
  margin-top: 0;
}

.forms{
  width: 350px;
  margin: auto;
  margin-bottom: 0;
}

.icons{
  color: white;
  height: 40px;
  width: auto;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 5px;
}

input{
  width: 250px;
  padding: 10px;
  outline: none;
  border: none;
}

.square {
  background: rgba(255, 255, 255, 0);
  border: 1px solid #999;
  float: left;
  font-size: 32px;
  font-weight: bold;
  line-height: 34px;
  height: 60px;
  padding: 0;
  text-align: center;
  width: 60px;
}



.game {
  margin: auto;
  margin-top: 100px;
  width: 480px;
  height: 480px;
  top: 100vh;
}

.submitt {
  width: 100px;
  height: 40px;
  margin-top: 10px;
  margin-left: 210px;
  color: white;
  background-color: #04E824;
  border-color: #04E824;
  border-radius: 20px;
  border-style: solid;
  font-size: 20px;
  font-family: 'Poppins';
}

.background {
  font-size: 1000px;
  color: rgba(238, 52, 83, 0.466);
  position: fixed;
  top: -155vh;
  left: 10vw;
  z-index: -9999;

}
.background2 {
  font-size: 1000px;
  color: rgba(238, 52, 83, 0.466);
  position: fixed;
  top: -155vh;
  left: 60vw;
  z-index: -9999;
}

.turn {
  width: 100%;
  font-size: 10px;
}


